@import url(https://fonts.googleapis.com/css?family=Playfair+Display);
@import url(https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  font-family: 'Playfair Display', serif;
  text-align: center;
  margin: auto;
  overflow-x: hidden;
}
.loadPage {
  margin-top: 5%;
  width: 50%;
  border-radius: 50%;
}
.no-margin {
  margin: 0;
}
.no-padding {
  padding: 0;
}
/* ====================HEADER================= */
.header {
    text-align: left;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    width: 100%;
    height: 30px;
    background-color: rgb(34, 102, 102);
    padding: 5px 0;
    color: lightgrey;
    font-family: 'Open Sans Condensed', sans-serif;
    letter-spacing: 2px;
    position: fixed;
}
.menuIcon {
    float: left;
    height: 30px;
    width: 30px;
    cursor: pointer;
    margin-left: 20px;
}
.userInfo {
    margin: 0 13% 0 0;
}
.userName {
    display: inline;
    float: right;
    margin: 0 2% 0 0;
    font-weight: 200;
}
.userImg {
    display: inline;
    float: right;
    width: 30px;
    height: 30px;
    -webkit-transition: all 500ms;
    transition: all 500ms;
}

/* ====================CONTENT================= */

.mainSection {
    text-align: center;
    color: slategrey;
    min-height: 600px;
    height: auto;
    padding-top: 30px;
    padding-bottom: 60px;
    background-color: rgba(34, 102, 102, 0.1);
    width: 75%;
    margin: auto;
}

/* ====================FOOTER================= */

.footer {
    width: 100%;
    height: 60px;
    background-color: rgb(34, 102, 102);
    color: slategrey;
}

/* ====================LOADER================= */
.loader {
    text-align: center;
}
.loaderImage {
    width: 50%;
}
/* ====================RESPONSIVE================= */
@media only screen and (max-width: 650px) {
    .mainSection {
        min-height: 550px;
        width: 100%;
    }
    .loader {
        left: 35%;
    }
}
@media only screen and (max-width: 600px) {
    .mainSection {
        padding-top: 50px;
    }
    .header {
        height: 50px;
        grid-template-rows: 50px;
    }
    .userImg, .userName {
        text-align: center;
    }
    .userName {
        width: 100px;
    }
    .userImg {
        margin-top: 1%;
    }
    .userImg, .menuIcon {
        width: 40px;
        height: auto;
    }
}
@media only screen and (max-width: 500px) {
    .userName {
        visibility: hidden;
    }
}
@media only screen and (max-width: 450px) {
    .mainSection {
        height: auto;
        width: 100%;
    }
}
@media only screen and (max-width: 370px) {
    .userImg {
        display: none;
    }
}
.loginPage {
    background-image: -webkit-linear-gradient(top, rgb(34, 104, 104), rgb(104, 104, 104));
    background-image: linear-gradient(to bottom, rgb(34, 104, 104), rgb(104, 104, 104));
    text-align: center;
    height: 500px;
    padding: 20%;
}
.login {
    outline: none;
    background-color: transparent;
    border: 2px solid slategrey;
    cursor: pointer;
    color: rgb(34, 104, 104);
    height: 30%;
    width: 65%;
}
.btn-login:hover {
    box-shadow: 0 0 3px 5px slategrey;
}
.searchBar {
    display: inline;
    float: left;
    margin-left: 9%;
    border-radius: 50px;
    outline: none;
    border: none;
    padding: 4px 10px;
    width: 50%;
    text-align: left;
    height: 23px;
    font-family: 'Open Sans Condensed', sans-serif;
    font-weight: 600;
    font-size: 1em;
    background-color: rgba(255, 255, 255 , 0.3);
    -webkit-transition: all 500ms;
    transition: all 500ms;
}
.searchBar::-webkit-input-placeholder {
    color: black;
    -webkit-transition: all 500ms;
    transition: all 500ms;
}
.searchBar::-ms-input-placeholder {
    color: black;
    -webkit-transition: all 500ms;
    transition: all 500ms;
}
.searchBar::placeholder {
    color: black;
    -webkit-transition: all 500ms;
    transition: all 500ms;
}
.searchBar:focus {
    background-color: rgba(255, 255, 255 , 0.9);
    color: slategrey;
    -webkit-transition: all 500ms;
    transition: all 500ms;
}
.searchBar:focus::-webkit-input-placeholder {
    -webkit-transition: all 500ms;
    transition: all 500ms;
    color: slategrey;
}
.searchBar:focus::-ms-input-placeholder {
    -webkit-transition: all 500ms;
    transition: all 500ms;
    color: slategrey;
}
.searchBar:focus::placeholder {
    -webkit-transition: all 500ms;
    transition: all 500ms;
    color: slategrey;
}
.searchBar::-moz-selection {
    background-color: rgba(34, 102, 102, 0.3);
    color: slategrey;
}
.searchBar::selection {
    background-color: rgba(34, 102, 102, 0.3);
    color: slategrey;
}

@media only screen and (max-width: 600px) {
    .header {
        height: 50px;
        grid-template-rows: 50px;
    }
    .searchBar {
        height: 40px;
    }
    .userImg, .userName {
        text-align: center;
    }
    .userName {
        width: 100px;
    }
    .userImg {
        margin-top: 1%;
    }
    .userImg, .menuIcon {
        width: 40px;
        height: auto;
    }
}

/* ====================RESPONSIVE================= */
@media only screen and (max-width: 1000px) {
    .searchBar {
        width: 30%;
    }
}
@media only screen and (max-width: 500px) {
    .searchBar {
        width: 45%;
    }
}
.menu {
    position: fixed;
    top: 40px;
    width: 250px;
    background-color: rgba(34, 102, 102, 0.95);
    height: 100%;
    z-index: 999;
}
.visible {
    -webkit-transition: margin 350ms;
    transition: margin 350ms;
}
.hidden {
    margin-left: -250px;
    -webkit-transition: margin 350ms;
    transition: margin 350ms;
}
.menuItems {
    margin: 20px 10px;
    padding: 0;
}
a {
    text-decoration: none;
}
.menuItem {
    margin-top: 20px;
    padding-left: 10px;
    color: white;
    font-size: 1.5em;
    list-style-type: none;
    border: 3px solid transparent;
}
.menuItem:hover {
    border-left: 3px solid white;
}

@media only screen and (max-width: 600px) {
    .menu {
        top: 60px;
    }
    .visible {
        width: 100%;
        text-align: center;
    }
    .menuItems {
        margin-top: 30%;
    }
    .menuItem:hover {
        border-left: 3px solid transparent;
        font-weight: bold;
    }
}
.playlistsHolder {
    padding: 3%;
    display: grid;
    grid-template-columns: minmax(30%, 35%) minmax(30%, 35%) minmax(30%, 35%);
    grid-column-gap: 1%;
    grid-row-gap: 2%;
}
.playlistHolder {
    display: grid;
    grid-template-rows: 0.5fr 0.5fr 1fr;
}
.playlistName {
    font-size: 1.25em;
    height: 2.5em;
    margin: 0;
}
.playlistImage {
    margin: auto;
    width: 150px;
    height: auto;
}
.playlistTrack {
    list-style: none;
    word-break: break-word;
}
.firstLetterUppercase {
    max-width: 85%;
    margin: auto;
    border-left: 1px dashed slategrey;
    border-bottom: 1px dashed slategrey;
    border-radius: 10px;
    border-bottom-right-radius: 0;
}
.firstLetterUppercase:first-letter {
    text-transform: capitalize;
}
.pageNumbers-ul {
    zoom: 0.7;
    width: 65%;
    font-size: 1.5em;
    text-align: center;
    display: inline-block;
    margin: 0;
    padding-top: 15px;
    padding-bottom: 15px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.pageNumbers {
    display: inline;
    margin: 0;
    padding: 15px;
    text-decoration: none;
    color: rgb(34, 102, 102);
}
.pageNumbers:hover {
    border-bottom: 1px solid rgb(34, 102, 102);
}
.pageActive {
    font-weight: bold;
    font-size: 1.5em;
}
.updatePlaylists {
    position: absolute;
}

/* ====================RESPONSIVE================= */
@media only screen and (max-width: 650px) {
    .playlistsHolder {
        padding: 3%;
        display: grid;
        grid-template-columns: minmax(47%, 53%) minmax(47%, 53%);
        grid-column-gap: 1%;
        grid-row-gap: 2%;
    }
}
@media only screen and (max-width: 450px) {
    .playlistsHolder {
        padding: 3%;
        display: grid;
        grid-template-columns: 100%;
        margin-bottom: 250px;
    }
}
.songSearch{
    display: grid;
    grid-template-columns: 45% 45%;
    grid-template-rows: 30% 70%;
    grid-column-gap: 5%;
    width: 100%;
    margin: 0 1%;
}
.songSearchBar {
    font-family: 'Open Sans Condensed', sans-serif;
    font-size: 1em;
    font-weight: bolder;
    padding: 3px 5px;
    border: 2px solid rgb(34, 102, 102);
    color: rgb(34, 102, 102);
    height: 25px;
    width: 80%;
    outline: none;
}
.songSearchBar::-moz-selection {
    background-color: rgb(232, 239, 239);
}
.songSearchBar::selection {
    background-color: rgb(232, 239, 239);
}
.songSearchBar:focus::-webkit-input-placeholder {
    color: rgb(34, 102, 102);
}
.songSearchBar:focus::-ms-input-placeholder {
    color: rgb(34, 102, 102);
}
.songSearchBar:focus::placeholder {
    color: rgb(34, 102, 102);
}
.predictive {
    border: 2px solid rgb(34, 102, 102);
    border-top: none;
    background-color: white;
    width: 82%;
    text-align: left;
    margin: auto;
    -webkit-transition: all 1s;
    transition: all 1s;
}
.predictiveText {
    font-size: 0.8em;
    line-height: 1.5em;
    margin: 0;
    padding: 0 5px;
    list-style-type: none;
    cursor: pointer;
}
.predictiveText:hover {
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    background-color: rgb(34, 102, 102);
    color: white;
}
.songsHolder {
    width: 80%;
    background-color: white;
    margin: auto;
    height: 300px;
    overflow-y: scroll;
    border: 2px solid rgb(34, 102, 102);
}
.songsToAdd {
    list-style-type: none;
    text-align: left;
    cursor: pointer;
    padding: 2px 8px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.songsToAdd:hover {
    font-weight: bold;
}
.addToPlaylist {
    margin: 10px;
    width: 80%;
}
.active {
    background-color: rgb(34, 102, 102);
    color: white;
}
.newPlaylistSongs {
    width: 80%;
    background-color: white;
    margin: auto;
    height: 300px;
    overflow-y: scroll;
    border: 2px solid rgb(34, 102, 102);
}
.casual {
    display: grid;
    grid-template-columns: 85% 15%;
    grid-template-rows: auto auto;
    grid-auto-rows: 1fr;
    width: 100%;
    line-height: 25px;
    margin-top: 5px;
    text-align: left;
}
.songToAddName {
    margin: 0;
    padding-left: 5px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.remove {
    outline: none;
    align-self: center;
    background-color: transparent;
    border-radius: 50px;
    border: 2px solid rgb(34, 102, 102);
    color: rgb(34, 102, 102);
    padding: 0% 1%;
    width: 30px;
    height: 30px;
    cursor: pointer;
    font-size: 1em;
}
.remove:hover {
    font-weight: bold;
    border: 3px solid rgb(34, 102, 102);
}
.addPlaylist {
    margin-top: 100px;
    width: 90%;
}
.btn {
    font-family: 'Open Sans Condensed', sans-serif;
    font-size: 1em;
    font-weight: bold;
    padding: 8px;
    outline: 0;
    background-color: transparent;
    border: 2px solid rgb(34, 102, 102);
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.btn:hover {
    background-color: rgb(34, 102, 102);
    color: white;
}
.hr {
    width: 100%;
    height: 1px;
}
.searchBarMargin {
    margin-top: 10px;
}
/*===================MEDIA==============*/

@media only screen and (max-width: 500px) {
    .songSearch {
        grid-template-columns: 100%;
        grid-template-rows: 1fr;
    }
}
::-webkit-scrollbar {
    width: 10px;
}
::-webkit-scrollbar-track {
    background: transparent;
    border-radius: 10px;
}
/*::-webkit-scrollbar-track-piece {*/
    /*background: rgb(34, 102, 102);*/
/*}*/
::-webkit-scrollbar-thumb {
    background: rgb(34, 102, 102);
    /*border-radius: 50px;*/
}
/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: rgba(34, 102, 102, 0.9);
}

/*=================PLAYER===============*/
.playerWindow {
    padding: 2%;
}
.player {
    color: rgba(255, 0, 0, 0.6);
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    width: 600px;
    height: 350px;
    margin: 50px auto auto auto;
    background-image: -webkit-linear-gradient(300deg, rgb(14, 14, 14), rgb(34, 24, 24));
    background-image: linear-gradient(150deg, rgb(14, 14, 14), rgb(34, 24, 24));
    border-radius: 50px 50px 0 0;
    border: 4px double rgb(94, 24, 14);
    display: grid;
    grid-template-columns: 50% 50%;
    text-align: left;
    z-index: 999;
}
.currentSong {
    margin-left: 10px;
    padding-left: 5%;
    padding-top: 5%;
}
.currentSong > p {
    margin-top: 10%;
    text-align: center;
}
.currentSong > h3 {
    margin-top: 3%;
    background-color: rgb(15, 15, 15);
    font-size: 1.2em;
    box-shadow: 0 0 10px 10px rgb(15, 15, 15);
    height: auto;
    color: rgb(200, 0, 0);
    overflow: hidden;
}
.trackTime {
    margin-top: 70px;
    margin-bottom: 20px;
    font-size: 3em;
    background-color: rgb(15, 15, 15);
    letter-spacing: 5px;
    box-shadow: 0 0 10px 10px rgb(15, 15, 15);
}
.songInfo {
    text-align: center;
    padding: 5px;
}
.songInfo > img {
    width: 130px;
    height: auto;
    border-radius: 5px;
    margin-top: 5%;
    box-shadow: 0 0 10px 10px rgb(15, 15, 15);
}
.songInfoText {
    width: 80%;
    background-color: rgb(15, 15, 15);
    margin: 5% auto;
    height: 45px;
    overflow: hidden;
    box-shadow: 0 0 10px 10px rgb(15, 15, 15);
}
.buttonImage {
    width: 40px;
    height: 40px;
}
.playerButtons {
    box-shadow: 0 0 10px 10px rgb(15, 15, 15);
    background-color: rgb(15, 15, 15);
    text-align: center;
}
.playerButton {
background-color: rgb(15, 15, 15);
border: none;
outline: none;
margin-left: 2px;
}
.playerButton:active {
    zoom: 0.9;
}
.slider {
    margin-top: 5%;
}
/*Loader*/
.playerLoader {
    width: 50%;
    height: auto;
    margin-top: 25%;
}
.nogrid {
    display: flex;
}
#sliderInput {
    cursor: pointer;
    width: 100%;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    height: 5px;
    background: darkred;
    outline: none;
    -webkit-transition: all 500ms;
    transition: all 500ms;
    box-shadow: 0 0 5px 10px rgb(14, 14, 14);
}
#sliderInput::-moz-range-progress {
    background: darkred;
}
#sliderInput::-ms-fill-upper {
    background: red;
}
#sliderInput::-webkit-slider-thumb {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    height: 25px;
    width: 20px;
    background: darkred;
    border-radius: 100px;
    border: 2px solid black;
    cursor: pointer;
}
#sliderInput::-webkit-slider-thumb:hover {
    background: red;
}
#sliderInput:hover {
    background: rgb(200, 0, 0);
}
/*Loader UP*/
.arrow {
    float: right;
    margin-top: -30px;
    background-color: transparent;
    /*opacity: 0.8;*/
}
/*========================PLAYLIST===================*/
.player-playlists {
    display: block;
    background-color: rgb(15,15,15);
    height: 0;
    -webkit-transition: all 500ms;
    transition: all 500ms;
    z-index: -1;
    border-radius: 0px 0px 50px 50px;
    overflow: hidden;
    margin-top: -10px;
    border: none;
    background-image: -webkit-linear-gradient(45deg, rgb(14, 14, 14), rgb(34, 24, 24));
    background-image: linear-gradient(45deg, rgb(14, 14, 14), rgb(34, 24, 24));
}
.show {
    height: 400px;
    border-top: none;
    border: 4px double rgb(94, 24, 14);
    -webkit-transition: all 500ms;
    transition: all 500ms;
}
.songList {
    width: 100%;
    margin: 0;
    padding: 5px 3%;
    border-bottom: 2px solid rgb(94, 24, 14);
    height: 300px;
    overflow: scroll;
}
.songInList {
    display: grid;
    grid-template-columns: 8% 80% 12%;
    list-style-type: none;
    font-size: 1.5em;
    height: 70px;
    /*max-height: 70px;*/
    overflow: hidden;
    margin: 10px 3% 0 0;
}
.songInList:hover {
    color: rgb(250, 0, 0);
    background-color: rgb(24, 24, 24);
}
.playerPlaylistName {
    text-align: center;
    margin-bottom: 15px;
}
.imgLeft {
    float:left;
    margin-right: 20px;
}
.duration {
    float: right;
}
.songPlaying {
    -webkit-transition: all 300ms;
    transition: all 300ms;
    background-image: -webkit-linear-gradient(left, rgb(14, 14, 14), rgb(54, 14, 14), rgb(14, 14, 14));
    background-image: linear-gradient(90deg, rgb(14, 14, 14), rgb(54, 14, 14), rgb(14, 14, 14));
    border-radius: 50px;
}
.songPlaying:hover {
    color: red;
}
/*========================MEDIA=======================*/
@media only screen and (max-width: 850px) {
    .player {
        width: 95%;
    }
    .songInList {
        grid-template-columns: 15% 70% 15%;
        max-height: 500px;
    }
}
@media only screen and (max-width: 550px) {
    .player {
        grid-template-columns: 90%;
        height: 700px;
    }
    .player-playlists {
        height: 0;
        -webkit-transition: all 500ms;
        transition: all 500ms;
    }
    .arrowImg {
        width: 30px;
        height: 30px;
    }
    .show {
        height: 30em;
        -webkit-transition: all 500ms;
        transition: all 500ms;
    }
}
@media only screen and (max-width: 450px) {
    .songInList {
        grid-template-columns: 80% 20%;
        max-height: 500px;
    }
    .imgLeft {
        display: none;
    }
    .songPlaying {
        border-radius: 0;
    }
}
@media only screen and (max-width: 250px) {
    .arrow {
        margin-top: -45px;
        margin-right: -15px;
    }
}
.playlistGrid {
    margin-top: 4%;
    width: 100%;
    display: grid;
    grid-template-columns: 50% 50%;
}
.viewPlaylistImage {
    width: 300px;
    margin: 5%;
    border-radius: 5px;
    /*box-shadow: 0 0 7px 8px rgb(34, 102, 102);*/
}
.editPlaylistName {
    padding: 5px;
    font-size: 1.2em;
    color: slategrey;
}
.nameDiv {
    text-align: center;
    max-width: 300px;
    padding: 50px;
    margin-left: 12%;
}
.viewPlaylistName {
    word-break: break-word;
    text-align: center;
    display: inline;
}
.editImage {
    width: 15px;
    margin-left: 2%;
    display: inline;
    cursor: pointer;
}
.tracksGrid {
    margin-top: 1%;
    margin-bottom: 5%;
}
.viewPlaylistTracks {
    display: grid;
    grid-template-columns: 90% 7%;
    grid-column-gap: 3%;
    margin-top: 2%;
    text-align: left;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.tracks-ul {
    max-height: 350px;
    overflow-y: scroll;
}
.viewPlaylistTrack {
    display: inline-block;
    margin-top: 5px;
}
.removeTrack {
    border: none;
    height: 30px;
    background-color: transparent;
    display: inline-block;
    margin-right: 10px;
    font-weight: bold;
    font-size: 1em;
    color: rgb(155, 100, 100);
    outline: none;
    cursor: pointer;
}
.removeTrack:hover {
    color: red;
}
a {
    text-decoration: none;
    color: slategrey;
}
.mainColor {
    color: rgb(34, 102, 102);
}
.shorterButton {
    width: 80%;
    margin: auto;
}
/*=======================MEDIA==================*/
@media only screen and (max-width: 800px) {
    .playlistGrid {
        grid-template-columns: 100%;
    }
    .songsHeader {
        margin-top: 50px;
    }
    .viewPlaylistTracks {
        text-align: center;
        padding: 0;
    }
}
